import {takeLatest, call, put, delay, select} from "redux-saga/effects"
import {types as clippingTypes, actions as clippingActions} from "../reducers/reducer-clipping";
import {metaData} from "../reducers/reducer-video-load"
import uuid4 from 'uuid/v4'

export function* clipWatcherSaga(){
    yield takeLatest(clippingTypes.CREATE_CLIP, clipWorker);
}

function* clipWorker(action){    
    const id = uuid4()
    var thumbnail = null
    const meta = yield select(metaData)
    
    try{    
        if (meta){
            var totalImages = meta.seek_preview_images.high.images.length
            var width = meta.seek_preview_images.high.width
            var height = meta.seek_preview_images.high.height
            var rows = meta.seek_preview_images.high.rows
            var cols = meta.seek_preview_images.high.cols
            var interval = meta.seek_preview_images.high.interval
            var thumbsPerImage = rows * cols

            var thumbTime = (action.right + action.left)/2          // Choose the middle of clip for a thumbnail
            var totalThumbIndex = Math.floor(thumbTime/interval)    // Calculate the total thumnail number

            var imageIndex = Math.floor(totalThumbIndex/thumbsPerImage) // Calculate which image atlas has the thumnail
            var thumbIndex = totalThumbIndex % thumbsPerImage           // Within the image atlas, which thumnail is it            
            var rowIndex = Math.floor(thumbIndex/cols)
            var colIndex = thumbIndex % cols
            var xOffset = colIndex * width
            var yOffset = rowIndex * height
            
            thumbnail = {
                url: meta.seek_preview_images.high.images[imageIndex],
                xOffset: xOffset,
                yOffset: yOffset,
                width: width,
                height: height                
            }            
        }else{
            console.log("No meta data was found to create clip")
        }
    }
    catch(err){
        console.log("Unable to generate thumbnail for clip:", err)
    }  

    const clip = {        
        vodID: action.vodID,
        left: action.left,
        right: action.right,
        thumbnail: thumbnail
    }

    yield put(clippingActions.addClip(id, clip));
}