export const types = {
    CREATE_CLIP : 'CREATE_CLIP',
    ADD_CLIP: 'ADD_CLIP',
    DELETE_CLIP: 'DELETE_CLIP',
    CLEAR_CLIPS: 'CLEAR_CLIPS'   
};

export const actions = {
    createClip: (vodID, left, right) => ({
        type: types.CREATE_CLIP, 
        vodID: vodID,
        left: left,
        right: right       
    }),
    addClip: (id, clip) =>({type: types.ADD_CLIP, id:id, clip: clip}),
    deleteClip: (id) => ({type: types.DELETE_CLIP, id: id}),
    clearClips: ()=> ({type: types.CLEAR_CLIPS})   
}

export const initialState = {
    clips: {}
};

export default function(state = initialState, action){
    switch (action.type){        
        case types.ADD_CLIP:       
            return {...state, clips:{...state.clips, [action.id]:action.clip} }         
        case types.DELETE_CLIP:
            const {[action.id]: del, ...res} = state.clips
            return {...state, clips:res}
        case types.CLEAR_CLIPS:
            return {...state, clips:{}}
        default:
            return state;
    }
}