import React from 'react';

import ReactPlayer from 'react-player';

class VideoPlayer extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      playing: false
    }
  }

  ref = player => {
    this.player = player;
  };

  render() {    
    return (
      <ReactPlayer
        ref={this.ref}
        url={this.props.url}
        width="100%"
        height={this.props.height || "25%"}    
        playing={this.state.playing}    
        progressInterval={500}
        onProgress={ (progress) => {this.props.onVideoProgress && this.props.onVideoProgress(progress.playedSeconds)}}
        onDuration={ (duration) => {this.props.onVideoDuration && this.props.onVideoDuration(duration)}}
      />
    );
  }  

  componentDidUpdate(prevProps) {
    if (prevProps.contentOffset !== this.props.contentOffset) {
      this.seekTo(this.props.contentOffset);
    }
  }

  seekTo = (contentOffset) => {    
    this.player.seekTo(contentOffset);
    if (contentOffset){
      // There is currently a bug where on the first seek, the video will look like its playing
      // but there will actually be now playback. The user has to seek again in order for it 
      // to start playing. This is a bit jarring, so for now we defer to the user to control
      // the playback at all times.
      // this.setState({...this.state, playing:true})
    }    
  }
}

export default VideoPlayer;
