import React from 'react';
import styled from 'styled-components';
import {formatTime} from '../utils/string-utils'

//Components
import { Header, Button } from 'semantic-ui-react';
import _ from 'lodash'
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css"

const HighlightItem = styled.div`
    display: flex;
    align-items: center;
    flex-direction:column;
    justify-content: center;
    border: 1px solid lightgrey;
    border-radius:5px;
    padding: 5px;
    margin: 3px;  
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    transition: color 0.3s;
    transition: background 0.3s;

    &:hover{
        background:lightgrey;  
        cursor:pointer;
    }
    &:active{
        background:grey;  
    }
`
const TimeStamp = styled.div`
    font-size: 1.2em;
    font-weight: bold;
`
const HitCounter = styled.div`
    font-variant: small-caps;
`
const TopHeader = styled.div`
    display:flex;
    justify-content: space-between;
    align-items:center;   
    margin-bottom: 2px;
`

class TopSearchResults extends React.Component {
    constructor(props){
        super(props);           
    }

    seekTo = (seconds) =>{       
        if (this.props.seekTo){
            this.props.seekTo(seconds)
        }        
    }

    renderTopComments = highlights =>{
        const handleOnDragStart = e => e.preventDefault()
        return highlights.map( (b,idx) => {
            return (
                <HighlightItem key={idx} onDragStart={handleOnDragStart} onClick={()=>this.seekTo(b.val)}>                    
                    <TimeStamp> ~ {formatTime(b.val)} </TimeStamp>
                    <HitCounter> {b.count} comments </HitCounter>        
                </HighlightItem>
            )
        })
    }    
    
    render() {        
        const highlights = this.props.topHighlights

        return (    
            <div style={{'paddingBottom':'10px', 'borderBottom':'1px solid lightgray ' }}>
                <TopHeader> 
                    <div>                         
                        {this.props.query=="" ? (
                            <span> Showing top timestamps overall </span>
                        ):(
                            <span> Showing top timestamps for <span style={{color:'#a333c8', fontWeight:'bold'}}> {this.props.query} </span> </span>
                        )}
                    </div>
                    <Button.Group size='mini'>
                        <Button onClick={()=>this.Carousel._slidePrev()} icon='angle left'/> 
                        <Button onClick={()=>this.Carousel._slideNext()} icon='angle right'/>
                    </Button.Group>
                </TopHeader>            
                <AliceCarousel ref={el=> this.Carousel=el} buttonsDisabled dotsDisabled infinite={false} responsive={{540: { items: 3 }}} >
                   {this.renderTopComments(highlights)}
                </AliceCarousel>
               
            </div>
        );
    }
}

export default TopSearchResults;
