import React from 'react';
import styled from 'styled-components';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux';
import {formatTime, formatDuration} from '../utils/string-utils'

//Components
import { Segment, Header, Icon, Item, Image, Label, Button, Statistic } from 'semantic-ui-react';

//Actions
import { actions as clippingActions } from "../reducers/reducer-clipping";

const ThumbnailImage = styled.div`   
    margin: 0px 5px 0px 0px;
    width: ${props => props.width ? `${props.width}px` : '220px'};
    height:${props => props.height ? `${props.height}px` : '140px'};
    background-size: ${props=> (props.x>=0 && props.y>=0) ? 'initial': 'contain'};
    background-position: ${props => (props.x>=0 && props.y>=0) ? `-${props.x}px -${props.y}px`: 'center'};
    background-image: ${props => props.url ? `url("${props.url}")` : `url("https://react.semantic-ui.com/images/wireframe/image.png")`};
`

class ClipTab extends React.Component {
    constructor(props){
        super(props);        
    }
   
    renderClips = (results) =>{
        if (Object.keys(results).length ==0){
            return (
                <Segment placeholder>
                    <Header icon>
                        <Icon name='clipboard list' />
                        Your Clips 
                        <Header.Subheader>Use the timeline to find and clip parts of the video to save for later</Header.Subheader>
                    </Header>                                  
                </Segment> 
            )
        }

        return(
            <div>
                <Segment textAlign='center'>
                    
                    <Statistic style={{margin:'0px'}}size='mini'>
                        <Statistic.Value>{Object.keys(results).length}</Statistic.Value>
                        <Statistic.Label>Clips</Statistic.Label>
                    </Statistic>

                    <Statistic size='mini'>
                        <Statistic.Value> 
                            <Icon name='clock outline' />
                            {
                                formatDuration
                                (
                                    Object.keys(results).map( (val)=>{
                                        return results[val].right - results[val].left
                                    }).reduce( (total,val)=> {return total+val})
                                , 'hour','min','sec')
                            }
                        </Statistic.Value>
                        <Statistic.Label>Total Duration</Statistic.Label>
                    </Statistic>                   
                                
                </Segment> 
                <Item.Group divided>
                    {
                        Object.keys(results).map( (key,idx)  => {
                            const clip = results[key]
                            const {thumbnail} = clip                            
                            return (
                                <Item key={idx}>
                                    {thumbnail ? (
                                        <ThumbnailImage width={thumbnail.width} height={thumbnail.height} x={thumbnail.xOffset} y={thumbnail.yOffset} url={thumbnail.url}/>      
                                    ):(
                                        <ThumbnailImage/>      
                                    )}
                                    
                                    <Item.Content>
                                        <Item.Header>{formatTime(clip.left)} - {formatTime(clip.right)}</Item.Header>
                                        <Item.Meta>
                                            <span className='cinema'>{formatDuration(clip.right-clip.left)}</span>
                                        </Item.Meta>
                                        <Item.Description>                                            
                                            <Button 
                                                icon size='mini' labelPosition='left'
                                                onClick={()=>{this.props.seekTo(clip.left)}}
                                            >
                                                Play 
                                                <Icon name='play' />                                                                                               
                                            </Button>
                                            <Button 
                                                icon size='mini' labelPosition='left'
                                                onClick={()=>{this.props.deleteClip(key)}}
                                            >
                                                Delete
                                                <Icon color='red' name='trash' />                                                
                                            </Button>
                                        </Item.Description>
                                        <Item.Extra>                                           
                                            
                                        </Item.Extra>
                                    </Item.Content>
                                </Item>
                            )
                        })
                    }
                </Item.Group>

            </div>           
        )      
    }

    render() {   
        const {clips} = this.props.vodClips;      
        return (
            <div>                    
                
               {this.renderClips(clips)}
                                 
            </div>
        );
    }
}

// Map Redux State and Actions to component props
function mapStateToProps(state) {
  return {
    vodClips: state.vodClips
  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      ...clippingActions
    },
    dispatch
  )
}



export default connect(mapStateToProps, matchDispatchToProps)(ClipTab);
