import {takeLatest, call, put, delay} from "redux-saga/effects"
import {types as loadVideoTypes, actions as loadVideoActions} from "../reducers/reducer-video-load";
import axios from 'axios';

export function* videoLoadWatcherSaga(){
    yield takeLatest(loadVideoTypes.LOAD_VIDEO_INFO, loadVideoWorker);
}

function* loadVideoWorker(action){
    var vodInfoResults = yield axios({
        method: 'get',
        url: `/api/vodInfo/${action.vodID}`,        
    })
    .then( (response) => { return response.data.data} ) 
    .catch( (error) => {console.log(error, error.response)})
   
    if (vodInfoResults){
        const seekPreviewQualities = (vodInfoResults.seek_preview_images || []).reduce(
            (obj, item) => {
                obj[item.quality] = item
                return obj
            }, {}
        )        
        const results = {...vodInfoResults, seek_preview_images: seekPreviewQualities }        
        yield put(loadVideoActions.updateVideoInfo(results))
    }    
}