export const types = {
    LOAD_VIDEO_INFO: 'LOAD_VIDEO_INFO',
    UPDATE_CURRENT_VODID: 'UPDATE_CURRENT_VODID',
    UPDATE_VIDEO_DURATION : 'UPDATE_VIDEO_DURATION', 
    UPDATE_VIDEO_INFO: 'UPDATE_VIDEO_INFO',
    SET_VIDEO_PROGRESS: 'SET_VIDEO_PROGRESS',
    CLEAR_VIDEO_INFO: 'CLEAR_VIDEO_INFO'  
};

export const actions = {
    loadVideoInfo: (vodID) => ({type:types.LOAD_VIDEO_INFO, vodID:vodID}),
    updateVideoInfo: (vodInfo) => ({type:types.UPDATE_VIDEO_INFO, vodInfo:vodInfo}),
    updateCurrentVodID: (vodID) => ({type: types.UPDATE_CURRENT_VODID, vodID: vodID}),
    updateVideoDuration: (duration) => ({type: types.UPDATE_VIDEO_DURATION, duration: duration}),
    setVideoProgress: (seconds) => ({type: types.SET_VIDEO_PROGRESS, seconds: seconds}),
    clearVideoInfo: () => ({type: types.CLEAR_VIDEO_INFO})
}

export const initialState = {
    vodID: null,
    isLoading: true,
    vodDuration: null,
    currentProgress: 0,
    metaData: null
};

function checkLoadingStatus(state){    
    return (state.vodID==null) || (state.vodDuration==null)
}

// Selectors
export const vodDuration = state => state.vodInfo.vodDuration
export const metaData = state => state.vodInfo.metaData

export default function(state = initialState, action){
    switch (action.type){
        case types.UPDATE_CURRENT_VODID:
            var s =  {...state, vodID: action.vodID};
            s.isLoading = checkLoadingStatus(s)
            return s
        case types.UPDATE_VIDEO_DURATION:
            var s = {...state, vodDuration: action.duration};
            s.isLoading = checkLoadingStatus(s)
            return s
        case types.SET_VIDEO_PROGRESS:
            return {...state, currentProgress: action.seconds}
        case types.UPDATE_VIDEO_INFO:
            return {...state, metaData: action.vodInfo}    
        case types.CLEAR_VIDEO_INFO:
            return {vodID: null, isLoading: true, vodDuration: null, currentProgress:0}
        default:
            return state;
    }
}