import React from 'react';
import styled from 'styled-components';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux';

//Components
import { Segment, Header, Icon, Button } from 'semantic-ui-react';
import SearchBar from '../components/search-bar'
import SearchResults from '../components/search-results'
import TopComments from '../components/search-top-comments'

//Actions
import { actions as searchActions } from "../reducers/reducer-search";

class SearchTab extends React.Component {
    constructor(props){
        super(props);        
    }
    onAddSearch = (request) => {        
        this.props.addSearch(this.props.vodID, request.query, request.userFilters)
    }
    onRemoveSearch = (id) =>{
        this.props.removeSearchResult(id);
    }
    renderSearchResults = (results) =>{
        if (Object.keys(results).length ==0){
            return (
                <Segment placeholder>
                    <Header icon>
                        <Icon name='search' />
                        Search for Chat Highlights
                        <Header.Subheader>Find specific phrases that appear in your video and use the timeline to navigate between them</Header.Subheader>
                    </Header>                                  
                </Segment> 
            )
        }

        return(
            <div style={{marginTop:"1em"}}>    
                <Header as='h3'>
                    Top Timestamps
                </Header>                  
                <SearchResults results={results} onRemoveSearch={this.onRemoveSearch} seekTo={this.props.seekTo} />             
            </div>           
        )      
    }

    

    render() {   
        const {searchResults, topComments} = this.props.search;   

        const selectedTerms = Object.keys(searchResults).map(key=>{
            const res = searchResults[key]
            return {
                query: res.query=="" ? "All": res.query,
                count: res.totalResults,
                id: key
            }
        })

        const selectedQueries = selectedTerms.map(term=> term.query)

        const topTerms =topComments.filter(comment=>{
            return !selectedQueries.includes(comment.Val)
        }).map(comment=>{
            return {
                query: comment.Val,
                count: comment.Count
            }
        })

        return (
            <div>     
                <SearchBar 
                    onAddSearch={this.onAddSearch} 
                    userFilters={["joe", "Nightbot"]}                    
                    loading= {this.props.search.isLoading}
                    placeholder = {"Add your own popular comments like \"pogchamp\"..."}
                />   
                <TopComments 
                    removeSearch={this.onRemoveSearch}
                    addSearch={this.onAddSearch}
                    selectedTerms= {selectedTerms}
                    topTerms = {topTerms}
                    loading={this.props.search.isLoading}
                />                         
                {this.renderSearchResults(searchResults)}                                 
            </div>
        );
    }
}

// Map Redux State and Actions to component props
function mapStateToProps(state) {
  return {
    search: state.search
  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      ...searchActions
    },
    dispatch
  )
}



export default connect(mapStateToProps, matchDispatchToProps)(SearchTab);
