import {takeLatest, call, put, delay, select, all} from "redux-saga/effects"
import {types as searchTypes, actions as searchActions} from "../reducers/reducer-search";
import {vodDuration} from "../reducers/reducer-video-load"
import axios from 'axios';
import uuid4 from 'uuid/v4'

export function* searchWatcherSaga(){
    yield all([
        takeLatest(searchTypes.ADD_SEARCH, searchWorker),
        takeLatest(searchTypes.LOAD_TOP_COMMENTS, topCommentsWorker)
    ])
}

function* searchWorker(action){
    yield put(searchActions.updateSearchStatus(true));       
    const duration = yield select(vodDuration)

    if (duration==null){
        yield put(searchActions.updateSearchStatus(false));
        console.log("Video duration information couldn't be loaded, aborting search")
        return
    }
        
    var results = yield axios({
        method: 'get',
        url: "/api/comments/search",
        params: {
            vodID: action.vodID,
            queryMessage: action.queryMessage,
            userFilter: action.userFilters.length >0 ? action.userFilters.join(' ') : [],
            windowMax: Math.ceil(parseFloat(duration))
        }
    })
    .then( (response) => { return response.data} )
    .catch( (error) => {console.log(error.response.data.message)})

    if (results){
        results.query = action.queryMessage
        results.vodID = action.vodID
        results.userFilters = action.userFilters
        results.id = uuid4()
        results.vodDuration = duration
        results.commentWindowMin = 0
        results.commentWindowMax = Math.ceil(parseFloat(duration))
        yield put(searchActions.addSearchResult(results))
    }
    yield put(searchActions.updateSearchStatus(false));
}

function* topCommentsWorker(action){
    var results = yield axios({
        method: 'get',
        url: `/api/comments/highlights/${action.vodID}`,
        params: {
            limit:8
        }
    })
    .then( (response) => { return response.data} )
    .catch( (error) => {console.log(error.response.data.message)})

    if (results){
        yield put(searchActions.setTopComments(results.Highlights.Buckets));
    }
    
}