import React from 'react';
import styled from 'styled-components';

//Components
import { Grid, Dropdown } from 'semantic-ui-react';

const FilterDropdown = styled(Dropdown)`
    border:2px darkgrey solid !important;    
`

class BrowseVodFilters extends React.Component {    

    render() {     
        var gameFilters = (this.props.gameFilters || []).map(game => {
            return {key:game, value:game, text:game}
        })

        var streamerFilters = (this.props.streamerFilters || []).map(streamer => {
            return {key:streamer, value:streamer, text:streamer}
        })   
        return (                        
            <Grid columns={2} padded>    
                <Grid.Column>            
                    <FilterDropdown 
                        placeholder='Filter By Streamer' 
                        fluid search selection clearable scrolling
                        loading={this.props.isLoading}   
                        options = {streamerFilters}
                        onChange={(e, data)=>{this.props.onStreamerChange(data.value)}}                      
                        value={this.props.streamerFilterValue}/>  
                </Grid.Column>
                <Grid.Column>
                    <FilterDropdown 
                        placeholder='Filter By Game' 
                        fluid search selection clearable scrolling
                        loading={this.props.isLoading}
                        options = {gameFilters}
                        onChange={(e, data)=>{this.props.onGameChange(data.value)}}
                        value={this.props.gameFilterValue}/>
                </Grid.Column>
            </Grid>
        );
    }
}

export default BrowseVodFilters;
