import { combineReducers } from 'redux';
import BrowseLoadReducer from './reducer-browse-load';
import SearchReducer from "./reducer-search";
import VideoLoadReducer from './reducer-video-load'
import ClippingReducer from './reducer-clipping'
import ChatReplayReducer from './reducer-chat-replay'

const allReducers = combineReducers({
    vodBrowse: BrowseLoadReducer,
    search: SearchReducer,
    vodInfo: VideoLoadReducer,
    vodClips: ClippingReducer,
    vodChat: ChatReplayReducer
});

export default allReducers;
