import {takeLatest, call, put, delay, all, select} from "redux-saga/effects"
import {types as loadBrowseTypes, actions as loadBrowseActions} from "../reducers/reducer-browse-load";
import {filters} from "../reducers/reducer-browse-load"
import axios from 'axios';

export function* browseLoadWatcherSaga(){
    yield all([
        takeLatest(loadBrowseTypes.LOAD_BROWSE_VODS, loadBrowseWorker),
        takeLatest(loadBrowseTypes.CHANGE_GAME_FILTER, loadBrowseWorker ),
        takeLatest(loadBrowseTypes.CHANGE_STREAMER_FILTER, loadBrowseWorker )
    ]);
}

function* loadBrowseWorker(action){
    yield put(loadBrowseActions.onLoadingVods(true)); 
    
    const filt = yield select(filters)
    var browseResponse = yield yield axios({
            method: 'get',
            url: `/api/browse`,
            params: {
                game: filt.game,
                streamer: filt.streamer
            }
        })
        .then( (response) => { return response.data} )
        .catch( (error) => {console.log(error.response.data.message)})

    if (browseResponse) {
        yield put(loadBrowseActions.updateBrowseVods(browseResponse.vods));
        if(action.type == loadBrowseTypes.LOAD_BROWSE_VODS && filt.game=="" && filt.streamer ==""){
            yield put(loadBrowseActions.updateBrowseFilters(browseResponse.games, browseResponse.streamers))
        }
    }
    yield put(loadBrowseActions.onLoadingVods(false));
}
