import React from 'react';
import styled from 'styled-components';

//Components
import { Input, Segment, Header, Icon, Popup, Dropdown, Label, Form, Button, Grid } from 'semantic-ui-react';

class SearchBar extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            query:"",
            userFilters:[]           
        }
    }

    //Events
    onAddSearch = () => {
        if (this.props.onAddSearch){
            this.props.onAddSearch({
                query: this.state.query,
                userFilters: this.state.userFilters
            })
        }            
    }
    onSearchKeyPress = (e) => {
        if (e.keyCode == 13){
            this.onAddSearch()
        }
    }
    onSearchChange = (e, data) =>{
        this.setState({...this.state, query: data.value});
    }

    render() {        
        return (                        
            <Input 
                fluid
                onKeyDown={this.onSearchKeyPress} 
                onChange={this.onSearchChange}
                action={{ icon: 'plus', color:'purple', onClick:this.onAddSearch, loading:this.props.loading, disabled:this.props.loading }}
                value={this.state.query}
                placeholder={this.props.placeholder || 'Search Term ...'} 
            />                 
        );
    }
}

export default SearchBar;
