import React from 'react';
import styled from 'styled-components';

//Components
import { Icon, Accordion, Button, Checkbox, Popup, Label } from 'semantic-ui-react';
import TopSearchResults from '../components/search-top-results'

const ResultTitle = styled.div`
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding-bottom:3px;
    border-bottom: 1px dotted gray;
`

class SearchResults extends React.Component {
    constructor(props){
        super(props);     
        this.state = {inActiveIds: []}    
    }
    handleClick = (id) =>{       
        const {inActiveIds} = this.state
        
        if (inActiveIds.includes(id)){
            this.setState({inActiveIds: inActiveIds.filter(val => val!=id)})
        }else{
            this.setState({inActiveIds: [...inActiveIds, id]})
        }
    }   

    render() {        
        const {inActiveIds} = this.state;
        
        return (                
            <Accordion fluid styled exclusive={false}>
                {
                    Object.keys(this.props.results).map( (key,idx) =>{
                        const res = this.props.results[key]
                        return (
                            <React.Fragment key={idx}>
                                <Accordion.Title active={!inActiveIds.includes(key)} index={idx} style={{'padding':'5px','paddingBottom':'0px', 'borderTop':'3px ridge navy'}} onClick={()=>this.handleClick(key)}>
                                    <ResultTitle>
                                        <div>
                                            <Icon name='dropdown' />                                        
                                            {res.query =="" ? 'All Comments': res.query}  
                                        </div>                                     
                                        <div>    
                                            {res.userFilters.length >0 &&   
                                                <Popup trigger={<Icon name='filter' color='green'/>}>  
                                                    <label> User Filters Applied </label>
                                                    <div>
                                                        { res.userFilters.map( (user,idx) => {
                                                            return (<Label key={idx}> {user} </Label>)
                                                        })}
                                                    </div>
                                                </Popup>
                                            }
                                                    
                                            <Button.Group size='tiny'>                            
                                                <Button
                                                    icon='close'
                                                    label={{ basic: true, content: res.totalResults + " hits", pointing:false }}
                                                    labelPosition='left'
                                                    compact  size='mini'
                                                    color='red'
                                                    onClick={()=>{this.props.onRemoveSearch(key)}}
                                                />   
                                                                                       
                                            </Button.Group>
                                        </div>
                                    </ResultTitle>                                        
                                                           
                                </Accordion.Title>
                                <Accordion.Content active={!inActiveIds.includes(key)} style={{clear:'both', padding:'0.5em'}}>                                    
                                    <TopSearchResults topHighlights={res.topHighlights} query={res.query} seekTo={this.props.seekTo}/>                                    
                                </Accordion.Content>
                            </React.Fragment>
                        )
                    })
                }
            </Accordion>
        );
    }
}

export default SearchResults;
