/**
  Our TimelineScrub Component. Much of this code is borrowed from 
  ReacVis's Highlight component: https://github.com/uber/react-vis/blob/master/src/plot/highlight.js
  We modify the component to add functionality of a timeline (seeking and playback indicators)
 */

import React from 'react';
import AbstractSeries from 'react-vis/dist/plot/series/abstract-series'
import {getAttributeScale} from 'react-vis/dist/utils/scales-utils';
import {formatTime} from '../utils/string-utils'

class TimeLineScrub extends AbstractSeries {
  state = {
    dragging: false,
    brushArea: {top: 0, right: 0, bottom: 0, left: 0},
    brushing: false,
    panning: false,
    resizing: false,
    startLocX: 0,
    startLocY: 0,
    dragArea: null,
    preview:0
  };

  getLocs(evt) {
    const xLocN = evt.type === 'touchstart' ? evt.pageX : evt.offsetX;
    const yLocN = evt.type === 'touchstart' ? evt.pageY : evt.offsetY; 
    const xScale = getAttributeScale(this.props, 'x');
    const yScale = getAttributeScale(this.props, 'y');
    const {marginLeft, marginTop} = this.props;
    const xLoc = xScale.invert(xLocN - marginLeft);
    const yLoc = yScale.invert(yLocN - marginTop)      
    return {xLoc, yLoc};
  }

  _getDrawArea(xLoc, yLoc) {
    const {startLocX, startLocY} = this.state;
    const {
      enableX,
      enableY,
      highlightWidth,
      highlightHeight,
      innerWidth,
      innerHeight,
      marginLeft,
      marginRight,
      marginBottom,
      marginTop
    } = this.props;
    const plotHeight = innerHeight + marginTop + marginBottom;
    const plotWidth = innerWidth + marginLeft + marginRight;
    const touchWidth = highlightWidth || plotWidth;
    const touchHeight = highlightHeight || plotHeight;

    const xScale = getAttributeScale(this.props, 'x');
    const yScale = getAttributeScale(this.props, 'y');
    const maxWidth = xScale.invert(innerWidth);
    const minHeight = yScale.invert(innerHeight);
    const maxHeight = yScale.invert(0);    

    return {
      bottom: enableY ? Math.max(startLocY, yLoc) : minHeight,
      right: enableX ? Math.max(startLocX, xLoc) : maxWidth,
      left: enableX ? Math.min(xLoc, startLocX) : 0,
      top: enableY ? Math.min(yLoc, startLocY) : maxHeight
    };
  }

  _getDragArea(xLoc, yLoc) {
    const {enableX, enableY} = this.props;
    const {startLocX, startLocY, dragArea} = this.state;    

    return {
      bottom: dragArea.bottom + (enableY ? yLoc - startLocY : 0),
      left: dragArea.left + (enableX ? xLoc - startLocX : 0),
      right: dragArea.right + (enableX ? xLoc - startLocX : 0),
      top: dragArea.top + (enableY ? yLoc - startLocY : 0)
    };
  }

  _clickedOutsideDrag(xLoc, yLoc) {
    const {enableX, enableY} = this.props;
    const {
      dragArea,
      brushArea: {left, right, top, bottom}
    } = this.state;
    const clickedOutsideDragX = dragArea && (xLoc < left || xLoc > right);
    const clickedOutsideDragY = dragArea && (yLoc < top || yLoc > bottom);
    if (enableX && enableY) {
      return clickedOutsideDragX || clickedOutsideDragY;
    }
    if (enableX) {
      return clickedOutsideDragX;
    }
    if (enableY) {
      return clickedOutsideDragY;
    }
    return true;
  }
  
  startBrushing(e) {
    const {onBrushStart, onDragStart, drag} = this.props;
    const {dragArea} = this.state;
    const {xLoc, yLoc} = this.getLocs(e.nativeEvent); 
       
    const startArea = (dragging, resetDrag) => {
      const emptyBrush = {
        bottom: yLoc,
        left: xLoc,
        right: xLoc,
        top: yLoc
      };
      this.setState({
        ...this.state,
        dragging,
        brushArea: dragArea && !resetDrag ? dragArea : emptyBrush,
        brushing: !dragging,
        startLocX: xLoc,
        startLocY: yLoc
      });
    };

    const clickedOutsideDrag = this._clickedOutsideDrag(xLoc, yLoc);
    if ((drag && !dragArea) || !drag || clickedOutsideDrag) {
      startArea(false, clickedOutsideDrag);

      if (onBrushStart) {
        onBrushStart(e);
      }
      return;
    }

    if (drag && dragArea) {
      startArea(true, clickedOutsideDrag);
      if (onDragStart) {
        onDragStart(e);
      }
    }
  }

  stopBrushing(e) {
    const {brushing, dragging, brushArea} = this.state;
    // Quickly short-circuit if the user isn't brushing in our component
    if (!brushing && !dragging) {
      return;
    }
    const {onBrushEnd, onDragEnd, drag} = this.props;
    const noHorizontal = Math.abs(brushArea.right - brushArea.left) < 5;
    const noVertical = Math.abs(brushArea.top - brushArea.bottom) < 0.5;
   
    // Invoke the callback with null if the selected area was < 5px
    const isNulled = noVertical || noHorizontal;
    // Clear the draw area
    this.setState({
      brushing: false,
      dragging: false,
      brushArea: drag ? brushArea : {top: 0, right: 0, bottom: 0, left: 0},
      startLocX: 0,
      startLocY: 0,
      dragArea: drag && !isNulled && brushArea
    });

    if (brushing && onBrushEnd) {     
      onBrushEnd(!isNulled ? brushArea : null);
    }

    if (drag && onDragEnd) {
      onDragEnd(!isNulled ? brushArea : null);
    }
  }

  onBrush(e) {
    const {onBrush, onDrag, drag} = this.props;
    const {brushing, dragging} = this.state;
    const {xLoc, yLoc} = this.getLocs(e.nativeEvent);
    if (brushing) {
      const brushArea = this._getDrawArea(xLoc, yLoc);
      this.setState({brushArea});

      if (onBrush) {
        onBrush(brushArea);
      }
    }

    if (drag && dragging) {
      const brushArea = this._getDragArea(xLoc, yLoc);
      this.setState({brushArea});
      if (onDrag) {
        onDrag(brushArea);
      }
    }
  }

  // Timeline Navigation
  onNavigate(e){
    const {xLoc, yLoc} = this.getLocs(e.nativeEvent);    
    const {onNavigate, drag} = this.props;    
    const {brushing, dragging, brushArea} = this.state;

    const cancelled = (Math.abs(brushArea.right - brushArea.left) > 1 && !drag);
    
    if (!cancelled){        
      var navPoint = xLoc;
      if (drag){        
        if (Math.abs(this.state.startLocX - xLoc)>0){  // If we have changed the drag area           
          navPoint = brushArea.left     
        }
        else if (this.state.resizing){ // If the drag area wasn't changed but we clicked the resizer
          navPoint = brushArea.left
        }
      }        
      if (onNavigate){
        onNavigate(navPoint)
      } 
      this.setState({desiredPos: navPoint})     
    }     
  }
  onNavigatePreview(e){
    const {xLoc, yLoc} = this.getLocs(e.nativeEvent);       
    this.setState({preview: xLoc})            
  }

  // Timeline Panning
  startPanning(e){      
    const { enableX, enableY } = this.props; 
    const { xLoc, yLoc } = this.getLocs(e.nativeEvent);  
    this.setState({       
      startLocX: xLoc,
      startLocY: yLoc,
      panning:true
    });
  }
  stopPanning(e){
    this.setState({panning:false})
  }
  pan(e){
    const {onPan} = this.props;
    if (this.state.panning){
      const { startLocX, startLocY } = this.state;
      const { enableX, enableY } = this.props; 
      const { xLoc, yLoc } = this.getLocs(e.nativeEvent);  
      const panData = {
        vertical: enableY ? startLocY - yLoc : 0,
        horizontal: enableX ? startLocX - xLoc : 0
      }
      if (onPan){
        onPan(panData)
      }
    }   
  }

  // Brush area resizing events
  startResize(e, intervalPos){
    const { xLoc, yLoc } = this.getLocs(e.nativeEvent);  
    this.setState({       
      startLocX: xLoc,      
      resizing:true,
      intervalPos: intervalPos
    });
  }
  stopResize(e){        
    this.setState({resizing:false})   
  }
  resize(e){
    const { xLoc, yLoc } = this.getLocs(e.nativeEvent);  
    if (this.state.resizing){
      if(this.state.intervalPos == "right"){
        const newRight = Math.max(this.state.brushArea.left, xLoc)
        this.setState({brushArea:{...this.state.brushArea, right: newRight}, dragArea:{...this.state.dragArea, right: newRight}})
      }     
      else if(this.state.intervalPos == "left"){
        const newLeft = Math.min(this.state.brushArea.right, xLoc)
        this.setState({brushArea:{...this.state.brushArea, left: newLeft}, dragArea:{...this.state.dragArea, left: newLeft}})
      }      
    }
  }

  // Component Lifecycle Events
  componentDidUpdate(prevProps){
    if ( Math.abs(this.props.cursor - this.state.desiredPos) < 20){
      this.setState({desiredPos:null})
    }
    if ( (this.props.drag != prevProps.drag) && this.props.drag==false){
      this.setState({brushArea : {top: 0, right: 0, bottom: 0, left: 0}})
    }
  }

  // Mouse Events
  onMouseDown(e){
    if (this.props.panning || e.button == 2){
      this.startPanning(e)
    } 
    else if (this.state.resizing){
      this.resize(e)
    }
    else{
      this.startBrushing(e)
    }    
  }
  onMouseMove(e){    
    this.pan(e);    
    this.resize(e)
    this.onBrush(e);        
    this.onNavigatePreview(e);
  } 
  onMouseUp(e){
    if (this.state.panning){
      this.stopPanning(e)      
    }
    else if (this.state.resizing){
      this.onNavigate(e)      
      this.stopResize(e)          
    }    
    else{
      this.stopBrushing(e);
      this.onNavigate(e)
    }
  }
  onMouseLeave(e){    
    this.stopPanning(e)  
    this.stopBrushing(e)      
  }

  // Public Reference Accessor methods
  getClipArea = () =>{    
    if (this.state.brushArea){      
      if(Math.abs(this.state.brushArea.left - this.state.brushArea.right) >0){        
        return this.state.brushArea
      }
    }
    return null;
  }

  render() {
    const {
      color,
      className,
      highlightHeight,
      highlightWidth,
      highlightX,
      highlightY,
      innerWidth,
      innerHeight,
      marginLeft,
      marginRight,
      marginTop,
      marginBottom,
      opacity
    } = this.props;
    var {
      brushArea: {left, right, top, bottom}
    } = this.state;

    const xScale = getAttributeScale(this.props, 'x');
    const yScale = getAttributeScale(this.props, 'y');

    let leftPos = 0;
    if (highlightX) {      
      leftPos = xScale(highlightX);
    }

    let topPos = 0;
    if (highlightY) {      
      topPos = yScale(highlightY);
    }

    const plotWidth = marginLeft + marginRight + innerWidth;
    const plotHeight = marginTop + marginBottom + innerHeight;
    const touchWidth = highlightWidth || plotWidth;
    const touchHeight = highlightHeight || plotHeight;

    //Convert all required display values to screen coordinates from domain coordinates.
    left = xScale(left) + marginLeft
    right = xScale(right) + marginLeft
    if (!this.props.enableY){
      bottom = touchHeight
      top = 0
    }else{
      bottom = yScale(bottom) + marginTop
      top = yScale(top)
    }   
   
    const cursorPos = xScale(this.props.cursor) + marginLeft;
    var desiredPos = this.state.desiredPos &&  ( xScale(this.state.desiredPos) + marginLeft )     
    var previewPos = xScale(this.state.preview) + marginLeft;
    
    return (
      <g
        transform={`translate(${leftPos}, ${topPos})`}
        cursor={ (this.props.panning || this.state.panning) ? 'grab' : 'auto'}
        className={`${className} rv-highlight-container`}
      >
        <rect
          className="rv-mouse-target"
          fill="black"
          opacity="0"
          x="0"
          y="0"
          width={Math.max(touchWidth, 0)}
          height={Math.max(touchHeight, 0)}
          onMouseDown={e => this.onMouseDown(e)}
          onMouseMove={e => {this.onMouseMove(e)}}
          onMouseUp={e => {this.onMouseUp(e)}}
          onMouseLeave={e => this.onMouseLeave(e)}
          // preventDefault() so that mouse event emulation does not happen
          onTouchEnd={e => {
            e.preventDefault();
            this.stopBrushing(e);
          }}
          onTouchCancel={e => {
            e.preventDefault();
            this.stopBrushing(e);
          }}
          onContextMenu={e => e.preventDefault()}
          onContextMenuCapture={e => e.preventDefault()}
        />

        <rect
          // Brush Area
          className="rv-highlight"
          pointerEvents="none"
          opacity={opacity}
          fill={color}
          x={left}
          y={top}
          width={Math.min(Math.max(0, right - left), touchWidth)}
          height={Math.min(Math.max(0, bottom - top), touchHeight)}
        />   
        {(right-left>0) && (
          <text 
            // Brush area size text
            pointerEvents="none" 
            x={(right-left <90) ? (left-4):(left + (right-left)/2)} 
            y={20} 
            fill="lawngreen" 
            textAnchor={(right-left<90) ?'end': 'middle'}
            style={{"userSelect":"none"}}
            fontSize={"10"}
            fontFamily={"Lato,'Helvetica Neue',Arial,Helvetica,sans-serif"}          
          > {formatTime(this.state.brushArea.left)}-{formatTime(this.state.brushArea.right)}</text>
        )}        
        <rect
          // Brush area resize handle left
          className="rv-highlight"
          pointerEvents="visiblePoint"
          cursor={'e-resize'}
          opacity={this.state.resizing? 0.5:0}
          fill={'steelblue'}
          x={left-2}
          y={top}
          width={ (this.props.drag && !this.state.brushing && (right-left)>0) ? 4:0}
          height={touchHeight}
          onMouseDown={(e)=> {this.startResize(e, "left")}}    
          onMouseUp={(e)=>{this.onMouseUp(e)}}      
        />  
        <rect
          // Brush area resize handle left
          className="rv-highlight"
          pointerEvents="visiblePoint"
          cursor={'e-resize'}
          opacity={this.state.resizing? 0.5:0}
          fill={'steelblue'}
          x={right-2}
          y={top}
          width={(this.props.drag && !this.state.brushing && (right-left)>0) ? 4:0}
          height={touchHeight}
          onMouseDown={(e)=> {this.startResize(e, "right")}} 
          onMouseUp={(e)=>{this.onMouseUp(e)}}  
        />  
        
        <rect
          // Playback cursor
          className="rv-highlight"
          pointerEvents="none"          
          opacity={0.5}
          fill={'tomato'}
          x={cursorPos-2}
          y={0}
          width={4}
          height={touchHeight}
        />    
        <text 
          // Playback cursor timestamp
          pointerEvents="none" 
          x={cursorPos+3} 
          y={touchHeight-5} fill="white" 
          fontSize={"10"}
          fontFamily={"Lato,'Helvetica Neue',Arial,Helvetica,sans-serif"}          
        > {formatTime(this.props.cursor)}</text>

        {desiredPos && (
          <rect
            // Desired Seek to position cursor
            className="rv-highlight"
            pointerEvents="none"
            opacity={0.5}
            fill={'green'}
            x={desiredPos-2}
            y={0}
            width={4}
            height={touchHeight}
          />              
        )}    
                
        <rect      
          // Seek preview cursor  
          className="rv-highlight"
          pointerEvents="none"
          opacity={0.5}
          fill={'grey'}
          x={previewPos-0.5}
          y={0}
          width={1}
          height={touchHeight}
        />    
        <text 
          // Seek preview cursor timestamp
          pointerEvents="none" 
          x={previewPos+3} 
          y={10} fill="white" 
          fontSize={"10"}
          fontFamily={"Lato,'Helvetica Neue',Arial,Helvetica,sans-serif"}         
        > {formatTime(this.state.preview)}</text>   

      </g>
    );
  }
}

TimeLineScrub.defaultProps = {
  color: 'rgb(77, 182, 172)',
  className: '',
  enableX: true,
  enableY: true,
  opacity: 0.3
};
export default TimeLineScrub;