import React from 'react';
import styled from 'styled-components';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux';
import {formatTime} from '../utils/string-utils'
import _ from 'lodash'

//Components
import { Tab, Icon, Header,Button, Popup } from 'semantic-ui-react';
import {HorizontalGridLines, VerticalGridLines, XAxis, FlexibleXYPlot, YAxis, LineSeries, DiscreteColorLegend } from 'react-vis';
import ReactResizeDetector from 'react-resize-detector';
import TimeLineScrub from '../components/timeline-scrub';
import 'react-vis/dist/style.css';

//Actions
import { actions as clippingActions } from "../reducers/reducer-clipping";

const TimelineContainer = styled.div`
  width:100%;
  height: ${props => props.height || "25%"};
  overflow-y:hidden;
  background:black;
  color:white;
  padding: 0.3em 0.5em 0.0em 0.3em;
  border-top: 1px tomato solid;
  position:relative;
`
const Loading = styled.div`
  background: rgba(0,0,0,0.6); 
  width:100%;
  height:100%;  
  display:flex;
  align-items: center;
  justify-content: center;
`
const Toolbar = styled.div`
  position:absolute;
  right:0px;
  top:0px;  
  display:flex;
  flex-direction:column;
`
const ToolBarButton = styled(Button)`
  width:1.5em;
  height:1.5em;
  padding:0px !important;
  margin:2px !important;
  background: ${props=> props.active ? "red !important": "black !important"};
  color:white !important;
  border: 1px solid white !important;
  transition: background 0.3s;
  transition: color 0.3s;
  &:hover{
    background: ${props=> props.active ? "red !important": "lightgrey !important"};
    color: ${props=> props.active ? "white !important": "black !important"};
  }
  &:active{
    color:grey !important;   
    border: 1px solid grey !important;   
  }
`
const Legend = styled(DiscreteColorLegend)`
  color:white !important;
  position:absolute;
  top:-5px;
  left:4em;
  .rv-discrete-color-legend-item__title{
    color:white !important;
  }
`

class Timeline extends React.Component {
  constructor(props){
    super(props)        
    this.state = {
      width: 0,
      filterWindow: null,
      isClipping: false,
      isPanning: false
    }

    this.axisStyle ={
      text: {fill:'white', fontSize:'0.7em'},
      line: {stroke: 'white'},
      ticks: {stroke: 'white'}
    }
    this.colorPalette = ['#F1C40F', '#27AE61', '#E77E23','#16A086', '#E84C3D', '#2A80B9', '#8F43AF', '#ECF0F1','#C1392B', '#D25400' ]       
    this.throttledWindow = _.throttle(this.setFilterWindow, 30)    
  } 

  /** FILTER WINDOW OPERATIONS **/ 

  /* Set the filter window for the timeline. Left and Right supply the min and max times 
     for the window. */
  setFilterWindow = (left, right) => {   
    var left = Math.max(0, left)
    var right = Math.min(this.props.vodInfo.vodDuration, right)    
    this.setState({...this.state, filterWindow:{left: left, right: right} })
  }
  computeY = () => {    
    var top = 1, bottom = 0
    const data = this.props.search.searchResults
    var lod = 60

    if (Object.keys(data).length !=0 && this.state.filterWindow){
      const {left, right} = this.state.filterWindow   
    
      var minIdx = Math.floor(left/lod)
      var maxIdx = Math.floor(right/lod)
     
      //Compute the maximum value of any series within the given time window
      top = Math.max( ...Object.keys(data).map(key => {
          return Math.max(...data[key].resultDensityMap[lod].slice(minIdx,maxIdx)) 
        }) )           
    }
    return [bottom, top]    
  }

  /* Resets the filter window to a default state */
  resetWindow = () =>{
    this.setFilterWindow(0, this.props.vodInfo.vodDuration)    
  }

  componentDidUpdate(){
    if (this.props.vodInfo.vodDuration && this.state.filterWindow ==null){
      this.resetWindow()
    }    
  }
  onWheelZoom = (delta) =>{
    if (this.state.filterWindow){
      const {left,right} = this.state.filterWindow;
      const dist = (right - left)/2 -30
      const factor = delta>0? Math.max(dist / 10,10) : dist/10

      if (delta!=0){
        var l = left - (delta/Math.abs(delta))*factor
        var r = right + (delta/Math.abs(delta))*factor      
        this.setFilterWindow(l,r)
      }    
    }    
  } 
  onPan = (pan) =>{
    if (this.state.filterWindow){
      const {left,right} = this.state.filterWindow;
      const newLeft = left + pan.horizontal
      const newRight = right + pan.horizontal
      if (newLeft >0 && newRight < this.props.vodInfo.vodDuration){
        this.throttledWindow(newLeft, newRight)
      }            
      
    }
  }


  onResize = (width,height)=>{
    this.setState({...this.state, width:width})
  } 

  onBrushEnd = (area) => {         
    if (area){
      if ( (area.right - area.left) > 120 ){
        this.setFilterWindow(area.left, area.right)
      }      
    } 
  }

  toggleClipping = () => {
    this.setState({...this.state, isClipping: !this.state.isClipping })
  }

  togglePanning = () => {
    this.setState({...this.state, isPanning: !this.state.isPanning})
  }

  renderSeries = (data, vodDuration) =>{
    if ( Object.keys(data).length == 0 ){      
      const emptyData = Array(Math.floor(vodDuration/60)).fill(0).map( (ele,i) => {return {x:i*60,y:0}} )
      return ( <LineSeries data={emptyData} curve={'curveMonotoneX'}/> )
    }
    else {
      return Object.keys(data).map((key,idx)=>{
        const res = data[key].resultDensityMap[60].map( (ele,i) => {return {x:i*60, y:ele}} )     
        return (
          <LineSeries key={idx} data={res} curve={'curveMonotoneX'} stroke={this.colorPalette[idx % this.colorPalette.length]}/>
        )
      })
    }    
  }
  renderLegend = (data) => {
    const items = Object.keys(data).map((key,idx)=>{
      return { title: data[key].query=="" ? "All" : data[key].query, color: this.colorPalette[idx % this.colorPalette.length] }
    })    
    return ( <Legend items={items} orientation="horizontal" /> )    
  }

  createClipping = (clipArea) =>{
    if (this.scrub){
      const clipArea = this.scrub.getClipArea()
      if (clipArea){
        this.props.createClip(this.props.vodInfo.vodID, clipArea.left, clipArea.right)
      }
    }    
  }

  render() {
    const {isLoading, vodDuration} = this.props.vodInfo;
    const data = this.props.search.searchResults
    const {filterWindow, isClipping, isPanning} = this.state       

    return (          
      <TimelineContainer height={this.props.height} onWheel={(e)=>{_.throttle(this.onWheelZoom,500)(e.deltaY)}}>
        { (isLoading || filterWindow==null)? (
          <Loading>                   
              <Header icon inverted>
                <Icon name='area chart' inverted/>
                Loading
              </Header>          
          </Loading>
        ): (
          <FlexibleXYPlot 
            animation 
            width={this.state.width}
            xDomain={filterWindow && [filterWindow.left, filterWindow.right]}  
            yDomain={this.computeY()}
            dontCheckIfEmpty          
            >
            
              <XAxis style={this.axisStyle} tickFormat={v => formatTime(v)} tickSize={2}/>
              <YAxis style={this.axisStyle} tickSize={2}/> 
              { this.renderLegend(data) }                                            
              { this.renderSeries(data, vodDuration) } 

              <TimeLineScrub
                drag={isClipping}
                panning={isPanning}
                color = 'white'                                       
                enableX={true}
                enableY={false}
                cursor={this.props.vodInfo.currentProgress}
                onBrushEnd={area => {!isClipping && this.onBrushEnd(area)}}
                onNavigate={seconds => this.props.seekTo(seconds)}
                onPan={pan=> this.onPan(pan)}   
                ref = {ref=>{this.scrub = ref}}             
              />            
              
          </FlexibleXYPlot>
        )}            
        <ReactResizeDetector handleWidth handleHeight onResize={this.onResize} />
       
        <Toolbar>  
          <ToolBarButton icon='arrows alternate horizontal' size='mini' onClick={this.resetWindow}/>
          <ToolBarButton active={isClipping} icon='mouse pointer' size='mini' onClick={()=> this.toggleClipping() }/>     
          <ToolBarButton active={isPanning} icon='arrows alternate' size='mini' onClick={()=> this.togglePanning()}/>  
          <ToolBarButton icon='clipboard' size='mini' onClick={()=> this.createClipping()}/>  
        </Toolbar>
      </TimelineContainer>    
         
    );
  }
}

// Map Redux State and Actions to component props
function mapStateToProps(state) {
  return {
    vodInfo: state.vodInfo,
    search: state.search
  }
}
function matchDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      ...clippingActions,      
    },
    dispatch
  )
}



export default connect(mapStateToProps, matchDispatchToProps)(Timeline);
