import React from 'react';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

//Components
import { Card, Icon, Image, CardGroup, Dropdown, Menu, Item, Dimmer, Loader, Segment } from 'semantic-ui-react'
import {TopLeftItemOverlay, TopRightItemOverlay} from '../components/overlays'

//Actions
import { actions as loadBrowseActions } from "../reducers/reducer-browse-load";

//Components
import BrowseVodGrid from '../components/browse-vod-grid'
import BrowseVodFilters from '../components/browse-vod-filters'
import Footer from '../components/footer'

class VodBrowser extends React.Component {

  componentDidMount() {
    this.props.loadBrowseVods();
    window.scrollTo(0,0)
  }

  render() {
    const {games, streamers, gameFilter, streamerFilter, vods, isBrowseLoading} = this.props.vodBrowse;
    return (
      <div>    
        <BrowseVodFilters 
          isLoading={isBrowseLoading}
          gameFilters={games}
          streamerFilters={streamers}
          gameFilterValue = {gameFilter}
          streamerFilterValue = {streamerFilter}
          onStreamerChange = {this.props.changeStreamerFilter}
          onGameChange = {this.props.changeGameFilter}
        />
        <BrowseVodGrid vods = {vods}/>

        <div style={{paddingTop:"1em"}}>
          <Footer/>
        </div>
      </div>
    )
  }
}
// Map Redux State and Actions to component props
function mapStateToProps(state) {
  return {
    vodBrowse: state.vodBrowse
  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      ...loadBrowseActions
    },
    dispatch
  )
}

export default connect(mapStateToProps, matchDispatchToProps)(VodBrowser);
