import React from 'react';
import styled from 'styled-components';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux';
import {formatTime, formatDuration} from '../utils/string-utils'

//Components
import { List, Segment, Header, Icon, Label } from 'semantic-ui-react';


class ChatTab extends React.Component {
    constructor(props){
        super(props);        
    }
   
    renderChat = (chatBuffer, currentProgress) =>{     
        var filteredMessages = chatBuffer.filter(item=> item.content_offset_seconds <= currentProgress+1) 
        return (
            <div>                 
                <Header> Total Chat: {filteredMessages.length}</Header>
                <List>
                    {filteredMessages.map( (item,idx) => {
                        return (
                            <List.Item key={idx}> 
                                <Label horizontal color='grey'> {formatTime(item.content_offset_seconds)} </Label>
                                <font color={item.message_user_color}> {item.commenter_display_name} </font>:                        
                                {item.message_body}                                 
                            </List.Item>
                        )
                    })}
                </List>
                <div style={{ float:"left", clear: "both" }}
                    ref={(el) => { this.messagesEnd = el; }}>
                </div>
            </div>
        )               
    }
    scrollToBottom = () => {
        this.messagesEnd.scrollIntoView({ behavior: "smooth" });
    }

    componentDidMount() {
        this.scrollToBottom();
    }

    componentDidUpdate() {
        this.scrollToBottom();
    }

    render() {   
        const {chatBuffer} = this.props.vodChat
        const {currentProgress} = this.props.vodInfo
        return (
            <div>            
               {this.renderChat(chatBuffer, currentProgress)}
            </div>
        );
    }
}

// Map Redux State and Actions to component props
function mapStateToProps(state) {
  return {
    vodChat: state.vodChat,
    vodInfo: state.vodInfo
  }
}

export default connect(mapStateToProps)(ChatTab);
