export const formatTime = (seconds) => {       
    var h = Math.floor(seconds / 3600).toString().padStart(2,'0')
    var m = Math.floor(seconds % 3600 / 60).toString().padStart(2,'0')
    var s = Math.floor(seconds % 3600 % 60).toString().padStart(2,'0')

    return [h,m,s].join(':')
}

export const formatDuration = (seconds, hl='hour', ml='minute', sl='second', pluralize=true) =>{
    var h = Math.floor(seconds / 3600)
    var m = Math.floor(seconds % 3600 / 60)
    var s = Math.floor(seconds % 3600 % 60)

    var hs ='', ms='', ss=''

    if (h>0){
        hs = `${h} ${hl}${ (h>1&&pluralize)?'s':''}`
    }
    if (m>0){
        ms = `${m} ${ml}${ (m>1&&pluralize)?'s':''}`
    }
    if (s>0){
        ss = `${s} ${sl}${ (s>1&&pluralize)?'s':''}`
    }

    return [hs,ms,ss].join(' ')
}