import _ from 'lodash';

export const types = {
    ADD_SEARCH : 'ADD_SEARCH',
    ADD_SEARCH_RESULT: 'ADD_SEARCH_RESULT',
    REMOVE_SEARCH_RESULT: 'REMOVE_SEARCH_RESULT',
    UPDATE_SEARCH_RESULT: 'UPDATE_SEARCH_RESULT',
    SEARCH_STATUS_UPDATE: 'SEARCH_STATUS_UPDATE',
    CLEAR_SEARCH_RESULTS: 'CLEAR_SEARCH_RESULTS',
    LOAD_TOP_COMMENTS:'LOAD_TOP_COMMENTS',
    SET_TOP_COMMENTS:'SET_TOP_COMMENTS',
    CLEAR_TOP_COMMENTS:'CLEAR_TOP_COMMENTS'
    
};

export const actions = {
    addSearch: (vodID, queryMessage, userFilters) => ({
        type: types.ADD_SEARCH, 
        queryMessage: queryMessage,
        userFilters: userFilters,
        vodID: vodID       
    }),
    addSearchResult: (result) => ({type: types.ADD_SEARCH_RESULT, payload: result}),
    removeSearchResult: (id) => ({type: types.REMOVE_SEARCH_RESULT, id: id}),
    updateSearchStatus: (isLoading) => ({type: types.SEARCH_STATUS_UPDATE, isLoading: isLoading}),  
    clearSearchResults: () => ({type: types.CLEAR_SEARCH_RESULTS}),
    loadTopComments: (vodID)=>  ({type: types.LOAD_TOP_COMMENTS, vodID:vodID}),
    setTopComments: (topComments)=> ({type: types.SET_TOP_COMMENTS, topComments:topComments}),
    clearTopComments: ()=>({type: types.CLEAR_TOP_COMMENTS})

}

export const initialState = {
    isLoading: false,    
    searchResults: {},
    topComments: []
};

export default function(state = initialState, action){
    switch (action.type){        
        case types.ADD_SEARCH_RESULT:           
            var resultObj = {}
            var payload = action.payload
            resultObj.topHighlights = _.orderBy(payload.resultDensityMap.buckets, ['count'], ['desc'])
                                        .filter(item => item.count>0)
                                        .slice(0,10)
            resultObj.resultDensityMap = {
                60: payload.resultDensityMap.buckets.map(hist => hist.count),
                10: Array(Math.floor(payload.vodDuration/10)).fill(0)
            }
            resultObj.comments = {
                windowMin: payload.commentWindowMin,
                windowMax: payload.commentWindowMax,
                results: payload.results
            }
            resultObj.totalResults = payload.resultsFound
            resultObj.query = payload.query
            resultObj.userFilters = payload.userFilters
            resultObj.vodID = payload.vodID
            resultObj.vodDuration = payload.vodDuration
            resultObj.timeSearched = Date.now()
            
            return {...state, searchResults: {...state.searchResults, [action.payload.id]:resultObj}};
        case types.REMOVE_SEARCH_RESULT:
            const {[action.id]: del, ...res} = state.searchResults
            return {...state, searchResults:res}
        case types.SEARCH_STATUS_UPDATE:
            return {...state, isLoading: action.isLoading}
        case types.CLEAR_SEARCH_RESULTS:
            return {...state, searchResults:{}}
        case types.SET_TOP_COMMENTS:
            return {...state, topComments: action.topComments}
        case types.CLEAR_TOP_COMMENTS:
            return {...state, topComments:[]}
        default:
            return state;
    }
}