import {takeLatest, call, put, delay, select} from "redux-saga/effects"
import { actions as videoLoadActions } from "../reducers/reducer-video-load";
import { actions as chatReplayActions} from "../reducers/reducer-chat-replay"


export const types = {
    ON_PROGRESS_UPDATE: 'ON_PROGRESS_UPDATE',   
};

export const actions = {  
    onProgressUpdate: (seconds, vodID) => ({type: types.ON_PROGRESS_UPDATE, seconds: seconds, vodID: vodID})   
}

export function* progressWatcherSaga(){
    yield takeLatest(types.ON_PROGRESS_UPDATE, progressWorker);
}

function* progressWorker(action){   
    yield put(videoLoadActions.setVideoProgress(action.seconds))
    yield put(chatReplayActions.chatVideoProgress(action.seconds, action.vodID))
    
}
