import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { bindActionCreators } from "redux";
import { connect } from 'react-redux';

//Components
import VideoPlayer from '../components/video-player';
import SplitterLayout from 'react-splitter-layout';
import SearchTab from '../containers/search-tab';
import ClipTab from '../containers/clip-tab';
import ChatTab from '../containers/chat-tab';
import Timeline from '../containers/timeline';
import { EmptyFullOverlay } from '../components/overlays';
import { Tab, Loader, Dimmer, Label, Menu, Sticky } from 'semantic-ui-react';

//Actions
import { actions as videoLoadActions } from "../reducers/reducer-video-load";
import { actions as searchActions } from "../reducers/reducer-search";
import { actions as clippingActions } from "../reducers/reducer-clipping";
import { actions as progressActions } from "../sagas/sagas-progress-router";

//CSS
import 'react-splitter-layout/lib/index.css';
import '../css/splitter.css';

const AfterContent = styled.div`
  position: absolute;
  top: 100vh;
  background: lime;
`
const SplitPane = styled.div`
  height:100%;
`

const TabPane = styled(Tab.Pane)`
  padding: 0.5em !important;      
  height:100%;
  overflow-y:auto;
`

const PaneContainer = styled.div`  
  display:flex;
  flex-direction: column;  
  height:100%; 
`

class VideoPage extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      dragging: false,
      activeItem: "Find Highlights"
    };
    
  }

  onDragStart = () => {
    this.setState({ dragging: true });
  }

  onDragEnd = () => {
    this.setState({ dragging: false });
  }

  handleItemClick = (e, { name }) => this.setState({...this.state, activeItem: name })

  componentDidMount(){
    this.props.updateCurrentVodID(this.props.match.params.id)
    this.props.loadVideoInfo(this.props.match.params.id)
    this.props.loadTopComments(this.props.match.params.id)    
  }
  
  componentWillUnmount(){
    this.props.clearVideoInfo();
    this.props.clearSearchResults();
    this.props.clearTopComments();
    this.props.clearClips();

  }
  
  seek = (ref)=>{
    if (ref){
      this.seekTo = ref.seekTo
    }    
  }  

  populateInitialSearch = ()=>{
    //Once the video duration has been loaded, we can populate the initial
    //search to show. In this case we show the "all comments" search.
    this.props.addSearch(this.props.match.params.id, "", [])
  }

  getClipTabMenu = (clips) => {
    return (
      <Menu.Item key='clips'>
        Clips
        {Object.keys(clips).length >0 && (
          <Label>{Object.keys(clips).length}</Label>
        )}        
      </Menu.Item>
    )
  }

  render() {
    const video_id = this.props.match.params.id;
    const createdUrl = `https://www.twitch.tv/videos/${video_id}`;
    const {isLoading} = this.props.vodInfo;
    const {clips} = this.props.clipInfo;
    const {activeItem} = this.state  
    
    return (      
      <div>       
          <SplitterLayout primaryMinSize={45} secondaryMinSize={25} secondaryInitialSize={30} onDragStart={this.onDragStart} onDragEnd={this.onDragEnd} percentage customClassName='clipply-splitter-layout'>    
              <SplitPane>              
                  {this.state.dragging && <EmptyFullOverlay blur/>}
                  <VideoPlayer 
                    height="75%" url={createdUrl} 
                    onVideoDuration={(duration)=> {this.props.updateVideoDuration(duration); this.populateInitialSearch();}} 
                    onVideoProgress={(secs)=> {this.props.onProgressUpdate(secs, video_id)}}
                    ref={this.seek} 
                  />                                   
                  <Timeline height="25%" seekTo={this.seekTo} />
              </SplitPane>   
              <SplitPane>   
                <Dimmer active={isLoading}>
                  <Loader> Loading Video Information </Loader>
                </Dimmer>    
                <PaneContainer>              
                                  
                  <Menu attached='top' tabular color='purple'>
                    <Menu.Item name='Find Highlights' active={activeItem === 'Find Highlights'} onClick={this.handleItemClick} />
                    <Menu.Item name='Clips' active={activeItem === 'Clips'} onClick={this.handleItemClick} />
                    <Menu.Item name='Chat Replay' active={activeItem === 'Chat Replay'} onClick={this.handleItemClick} />
                  </Menu>  
                           
                  <TabPane>
                    {activeItem === 'Find Highlights' && <SearchTab vodID={video_id} seekTo={this.seekTo}/> }
                    {activeItem === 'Clips' && <ClipTab seekTo={this.seekTo}/> }
                    {activeItem === 'Chat Replay' && <ChatTab seekTo={this.seekTo}/> }
                  </TabPane>
                  
                </PaneContainer>
                         
              </SplitPane>           
          </SplitterLayout>          
       
          <AfterContent>
            {/* Optional Additional content can go here (recommened videos etc.) User won't
                see this information unless they scroll. Kind of like full screen youtube 
                videos.
             */}                      
          </AfterContent>
      </div>
    );
  }
}

// Map Redux State and Actions to component props
function mapStateToProps(state) {
  return {
    vodInfo: state.vodInfo,
    clipInfo: state.vodClips
  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      ...videoLoadActions,
      ...searchActions,
      ...clippingActions,
      ...progressActions
    },
    dispatch
  )
}



export default connect(mapStateToProps, matchDispatchToProps)(VideoPage);
