export const types = {
    UPDATE_BROWSE_VODS : 'UPDATE_BROWSE_VODS',
    UPDATE_BROWSE_FILTERS: 'UPDATE_BROWSE_FILTERS',
    CHANGE_GAME_FILTER: 'CHANGE_GAME_FILTER',
    CHANGE_STREAMER_FILTER: 'CHANGE_STREAMER_FITLER', 
    CLEAR_ALL_FILTERS: 'CLEAR_ALL_FILTERS',   
    LOADING_VODS: 'LOADING_VODS',
    LOAD_BROWSE_VODS: 'LOAD_BROWSE_VODS'
};

export const actions = {
    updateBrowseVods: (vods) => ({type: types.UPDATE_BROWSE_VODS, payload: vods}),
    changeGameFilter: (game) => ({type: types.CHANGE_GAME_FILTER, game: game}),    
    changeStreamerFilter: (streamer) => ({type: types.CHANGE_STREAMER_FILTER, streamer: streamer}),
    clearAllFilters: () => ({type: types.CLEAR_ALL_FILTERS}),
    updateBrowseFilters: (games, streamers) => ({type: types.UPDATE_BROWSE_FILTERS, games: games, streamers: streamers}),
    onLoadingVods: (isLoading) => ({type: types.LOADING_VODS, payload: isLoading}),
    loadBrowseVods: () => ({type: types.LOAD_BROWSE_VODS})
}

export const filters = state =>{ return {game: state.vodBrowse.gameFilter, streamer: state.vodBrowse.streamerFilter} }


export const initialState = {
    vods: [],
    games: [],
    streamers: [],
    gameFilter: "",
    streamerFilter: "",
    isBrowseLoading: false
};

export default function(state = initialState, action){
    switch (action.type){
        case types.LOADING_VODS:
            return {...state, isBrowseLoading: action.payload};
        case types.UPDATE_BROWSE_FILTERS:
            return {...state, games: action.games, streamers: action.streamers}
        case types.UPDATE_BROWSE_VODS:
            // Format the date diff into a human readable value
            var vods = action.payload.map(vod => {
                var now = Date.now()
                var videoDate = new Date(vod.PublishedAt)
                var datediff = (now - videoDate) / 36e5
                if (datediff > 24) {
                datediff = Math.floor(datediff / 24) + " Days Ago "
                } else {
                datediff = Math.floor(datediff) + " Hours Ago"
                }
                return { ...vod, DateDiff: datediff }
            })
            return {...state, vods: vods};
        case types.CHANGE_GAME_FILTER:
            return {...state, gameFilter: action.game}
        case types.CHANGE_STREAMER_FILTER:
            return {...state, streamerFilter: action.streamer}
        case types.CLEAR_ALL_FILTERS:
            return {...state, streamerFilter:"", gameFilter:""}
        default:
            return state;
    }
}