import { all } from 'redux-saga/effects'
import * as browseLoading from "./sagas-browse-load"
import * as videoLoading from "./sagas-video-load"
import * as search from "./sagas-search"
import * as clipping from "./sagas-clipping"
import * as progressRouter from "./sagas-progress-router"
import * as chatReplay from "./sagas-chat-replay"

export default function* rootSaga(){
    yield all([
        browseLoading.browseLoadWatcherSaga(),
        search.searchWatcherSaga(),
        clipping.clipWatcherSaga(),
        videoLoading.videoLoadWatcherSaga(),
        progressRouter.progressWatcherSaga(),
        chatReplay.chatReplayWatcherSaga()
    ])
}