import React from "react";
import styled from "styled-components";
import { Link } from 'react-router-dom';
import { Responsive, Menu, Button, Container, Sidebar, Icon } from 'semantic-ui-react'

class MobileMenu extends React.Component {
    state = {sidebarOpened:false}

    handleSidebarHide = () => this.setState({ sidebarOpened: false })
    handleToggle = () => this.setState({ sidebarOpened: !this.state.sidebarOpened })

    render() {
        const { sidebarOpened } = this.state
        return (
            <Responsive maxWidth={Responsive.onlyMobile.maxWidth}>
                <Sidebar
                    as={Menu}
                    animation='push'
                    inverted
                    onHide={this.handleSidebarHide}
                    vertical
                    visible={sidebarOpened}
                >
                    <Menu.Item> <h2>Menu</h2> </Menu.Item>
                    <Link to="/"> <Menu.Item > Home  </Menu.Item> </Link>
                    {/*<Link to="/browse"> <Menu.Item > Browse  </Menu.Item> </Link>*/}
                    <Link to="/about"> <Menu.Item> Contact Us </Menu.Item> </Link>
                </Sidebar>
                <Menu
                    fixed='top'
                    size='mini'
                    inverted
                    style={{ "height": "40px" }}
                >
                    <Menu.Item onClick={this.handleToggle}>
                        <Icon name='sidebar' />
                    </Menu.Item>
                    <Menu.Item> <Link to="/"> <h2>Clipply</h2> </Link> </Menu.Item>
                    {/* TODO: Implement Login and Signup Buttons in the future */}
                    {/* <Menu.Item position='right'>
                        <Button as='a'>
                            Log in
                        </Button>
                        <Button as='a' color='purple' style={{ marginLeft: '0.5em' }}>
                            Sign Up
                        </Button>
                    </Menu.Item> */}
                </Menu>
            </Responsive>
        )
    }
}

class DesktopMenu extends React.Component {
    render() {
        return (
            <Responsive minWidth={Responsive.onlyTablet.minWidth}>
                <Menu
                    fixed='top'
                    size='small'
                    inverted
                    style={{ "height": "40px" }}
                >
                    <Menu.Item> <Link to="/"> <h2>Clipply</h2> </Link> </Menu.Item>
                    <Link to="/"> <Menu.Item style={{height:'100%'}}> Home </Menu.Item> </Link>
                    {/*<Link to="/browse"> <Menu.Item style={{height:'100%'}}> Browse </Menu.Item> </Link>*/}
                    <Link to="/about"> <Menu.Item style={{height:'100%'}}> Contact Us </Menu.Item> </Link>
                    {/* TODO: Implement Login and Signup Buttons in the future */}
                    {/* <Menu.Item position='right'>
                        <Button as='a'>
                            Log in
                        </Button>
                        <Button as='a' color='purple' style={{ marginLeft: '0.5em' }}>
                            Sign Up
                        </Button>
                    </Menu.Item> */}
                </Menu>
            </Responsive>
        )
    }
}

const MenuBar = ({ children }) => {
    return (
        <div>
            <DesktopMenu> </DesktopMenu>
            <MobileMenu> </MobileMenu>
        </div>
    );
};

export default MenuBar;
