import {takeLatest, call, put, delay, select} from "redux-saga/effects"
import {types as chatReplayTypes, actions as chatReplayActions} from "../reducers/reducer-chat-replay";
import axios from 'axios';

import {chatBuffer} from '../reducers/reducer-chat-replay'

export function* chatReplayWatcherSaga(){
    yield takeLatest(chatReplayTypes.CHAT_VIDEO_PROGRESS, chatReplayWorker);
}

var lastTime = 0;
var cursor = "";
var lastBatchMaxTime = 0
var lastBatchMinTime = 0

function* chatReplayWorker(action){  
    const buffer = yield select(chatBuffer)    
    if (buffer.length <=0 || Math.abs(lastTime - action.seconds)>5){        
        var results = yield axios({
            method: 'get',
            url: `/api/comments/stream/${action.vodID}`,
            params: {
                content_offset_seconds: Math.floor(action.seconds)
            }
        })
        .then( (response) => { return response.data} )
        .catch( (error) => {console.log(error.response.data.message)})
        
        if(results){
            yield put(chatReplayActions.setCommentsInBuffer(results.comments))
            lastBatchMaxTime = Math.max(...results.comments.map(item=> item.content_offset_seconds))
            lastBatchMinTime = Math.min(...results.comments.map(item=> item.content_offset_seconds))
            cursor = results.next
        }
        
    }
    else if ( (lastBatchMaxTime-action.seconds)<10 && cursor!="" ){
        var results = yield axios({
            method: 'get',
            url: `/api/comments/stream/${action.vodID}`,
            params: {
                cursor: cursor
            }
        })
        .then( (response) => { return response.data} )
        .catch( (error) => {console.log(error.response.data.message)})

        if(results){
            yield put(chatReplayActions.addCommentsToBuffer(results.comments))
            lastBatchMaxTime = Math.max( Math.max(...results.comments.map(item=> item.content_offset_seconds)), Math.max(...buffer.map(item=> item.content_offset_seconds)))
            lastBatchMinTime = Math.min( Math.min(...results.comments.map(item=> item.content_offset_seconds)), Math.min(...buffer.map(item=> item.content_offset_seconds)))
            cursor = results.next
        }       
    }

    lastTime = action.seconds
}