import React from 'react';
import styled from 'styled-components';

//Components
import { Button, Header, Label } from 'semantic-ui-react';

class TermHolder extends React.Component{
    constructor(props){
        super(props);   
        this.state = {
            hover: false
        }
    }  

    render(){        
        return (
            <Button 
                disabled={this.props.loading}  
                onClick={this.props.onClick}
                onMouseEnter={()=>this.setState({hover:true})} 
                onMouseLeave={()=>this.setState({hover:false})}
                size='mini' style={{margin:'3px'}}  as='div' labelPosition='right'
            >                   
                <Term active={this.props.active} ishovered={this.state.hover?1:0}>                        
                    {this.props.query}
                </Term>
                <Count active={this.props.active}  basic ishovered={this.state.hover?1:0}>
                    {this.props.count} Hits
                </Count>
            </Button>  
        )
    }
   
}
const Term = styled(Button)`
    border-radius: 3em 0 0em 3em !important;
    background: ${props => props.active ? (props.ishovered ?"rgba(225,200,224,0.5)": "rgba(225,200,224,1)"):(props.ishovered ?"rgba(225,200,224,0.5)": "white")} !important;
    color: rgb(112,61,156) !important;
    padding: 0.3em 1em 0.3em 1em !important;    
    font-weight:bolder !important;
    border: solid 1.2px ${props=> props.ishovered ? "black": (props.active?"grey": "lightgrey")} !important;
   
`
const Count = styled(Label)`
    border-radius: 0 3em 3em 0 !important;   
    background: ${props => props.active ? (props.ishovered ?"rgba(225,200,224,0.5)": "rgba(225,200,224,1)"):(props.ishovered ?"rgba(225,200,224,0.5)": "white")} !important;
    color: rgb(112,61,156) !important;   
    border: solid 1.2px ${props=> props.ishovered ? "black":  (props.active?"grey": "lightgrey")} !important;
    font-weight:900 !important;
`


class TopComments extends React.Component {
    constructor(props){
        super(props);       
    }    
    
    renderHighlightTerms = (selectedTerms, topTerms) => {        
        if (!selectedTerms || !topTerms) return;
        let selected =  selectedTerms.map( term => {
            return (    
                <TermHolder 
                    loading={this.props.loading}
                    key={term.id} 
                    active 
                    query={term.query} 
                    count={term.count} 
                    onClick={()=>this.props.removeSearch(term.id)}
                />                
            )
        })
        let top = topTerms.map( (term,idx) => {
            return (                
                <TermHolder 
                    loading={this.props.loading}
                    key={idx}
                    query={term.query} 
                    count={term.count} 
                    onClick={()=>this.props.addSearch({query:term.query, userFilters:[] })}
                />              
            )
        })
        return selected.concat(top)        
    }

    render() {                  
        return (  
            <div>
                <Header style={{marginBottom:"0.5em", marginTop:"0.5em"}}>
                    Select Top Comments 
                </Header>
                {this.renderHighlightTerms(this.props.selectedTerms, this.props.topTerms)}                                   
            </div>           
                   
        );
    }
}

export default TopComments;
