import _ from 'lodash'

export const types = {
    CHAT_VIDEO_PROGRESS : 'CHAT_VIDEO_PROGRESS',
    ADD_COMMENTS_TO_BUFFER: 'ADD_COMMENTS_TO_BUFFER',
    SET_COMMENTS_IN_BUFFER: 'SET_COMMENTS_IN_BUFFER'
};

export const actions = {
    chatVideoProgress: (seconds, vodID) => ({type: types.CHAT_VIDEO_PROGRESS, seconds: seconds, vodID: vodID}), 
    addCommentsToBuffer: (comments) => ({type: types.ADD_COMMENTS_TO_BUFFER, comments: comments}),
    setCommentsInBuffer: (comments) => ({type: types.SET_COMMENTS_IN_BUFFER, comments: comments}) 
}

export const chatBuffer = state => state.vodChat.chatBuffer

export const initialState = {
    chatBuffer: []    
};

export default function(state = initialState, action){
    switch (action.type){    
        case types.ADD_COMMENTS_TO_BUFFER:
            return {...state, chatBuffer: [...state.chatBuffer, ...action.comments].slice(-500,-1)}
        case types.SET_COMMENTS_IN_BUFFER:
            return {...state, chatBuffer: action.comments}         
        default:
            return state;
    }
}