import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

//Components
import { Image, Grid, Header, Icon } from 'semantic-ui-react';
import {TopLeftItemOverlay, TopRightItemOverlay} from '../components/overlays'

const UserImage = styled(Image)`
    position:absolute !important;
    display:table;
    bottom:1%;
    right:1%;
    margin:0.2em !important;
    width:4em;
    height:4em;
`

const VodContainer = styled.div`
    position:relative;
    transition: all 0.2s;
    
    &:hover{
       box-shadow: 3px 3px 10px rgba(0,0,0,0.45);
       transform: scale(1.01,1.01);
    }
`

const VodContentContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: flex-start; 
    margin-top: 0.3em;   
`


class BrowseVodGrid extends React.Component {    

    render() {        
        return (                        
            <Grid padded centered>
                {this.props.vods.slice(0, this.props.limit || this.props.vods.length).map(vod=>
                    <Grid.Column mobile={16} tablet={8} computer={5} largeScreen={4} widescreen={4} key={vod.VodID}>
                        <VodContainer>
                            <Link to={`/videos/${vod.VodID}`}>  
                                {vod.ThumbnailURL=="" ? (
                                    <Image fluid src={`https://via.placeholder.com/720x480/2c004d/FFFFFF?text=${encodeURIComponent(vod.DisplayName)}`} />
                                ):(
                                    <Image fluid src={vod.ThumbnailURL.replace("%{width}", "720").replace("%{height}", "480")} />
                                )}
                                
                            </Link>                            
                            <TopLeftItemOverlay>
                                <Icon name='play' size='small'/> {vod.Duration}
                            </TopLeftItemOverlay>
                            <TopRightItemOverlay>
                                <Icon name='clock outline' />{vod.DateDiff}
                            </TopRightItemOverlay>   
                            <UserImage src={vod.ProfileImageURL} circular/>                           
                        </VodContainer>     
                        <VodContentContainer>
                            <Image float="left" src={vod.BoxArtURL.replace("{width}", "50").replace("{height}", "70")} />                   
                            <Header as='h4' style={{textOverflow:"ellipsis", marginTop:"0", marginLeft:"0.3em"}}>
                                {vod.Title}
                                <Header.Subheader> {vod.DisplayName} </Header.Subheader>
                                <Header.Subheader> {vod.Game} </Header.Subheader>
                            </Header>                               
                        </VodContentContainer>                                
                    </Grid.Column>
                )}
            </Grid>             
        );
    }
}

export default BrowseVodGrid;
